import "./index.css";
import ToDoList from "./ToDoList";

const Form = ({ todos, setTodos, setInputText, inputText }) => {
  const inputTextHandler = (e) => {
    setInputText(e.target.value);
  };

  const submitTodoHandler = (e) => {
    e.preventDefault();
    setTodos([
      ...todos,
      { text: inputText, completed: false, id: Math.random() * 1000 },
    ]);
    setInputText("");
  };

  return (
    <div className="flex items-center justify-center">
      <div className="w-full mx-4 overflow-y-auto h-96 md:w-2/6 card md:mx-0">
        <div className="flex items-center justify-center">
          <form className="flex items-center justify-center w-11/12 my-8 ">
            <div className="flex items-center justify-center bg-white rounded-full">
              <input
                type="text"
                value={inputText}
                onChange={inputTextHandler}
                placeholder="mau ngapain hari ini?"
                className="h-10 pl-4 text-sm bg-transparent rounded-full focus:outline-none"
              />
              <button
                className="p-2 mr-1 rounded-full bg-darkPurple hover:bg-purple-800 focus:outline-none"
                type="submit"
                onClick={submitTodoHandler}>
                <svg
                  width="18"
                  height="18"
                  viewBox="0 0 18 18"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg">
                  <path
                    d="M17.3415 16.633C17.7052 16.633 18 16.939 18 17.3165C18 17.6625 17.7523 17.9485 17.4308 17.9938L17.3415 18H10.471C10.1073 18 9.81246 17.694 9.81246 17.3165C9.81246 16.9705 10.0602 16.6845 10.3816 16.6392L10.471 16.633H17.3415ZM10.6592 0.970537C11.906 -0.323512 13.9283 -0.323512 15.175 0.970537L16.4694 2.31395C17.7162 3.608 17.7162 5.7069 16.4694 7.00094L6.74061 17.0985C6.1843 17.6759 5.43007 17.9999 4.64282 17.9999H0.658541C0.288415 17.9999 -0.00901523 17.6834 0.000208899 17.2994L0.100432 13.1267C0.120357 12.3381 0.43127 11.5862 0.968667 11.0284L10.6592 0.970537ZM9.906 3.68365L1.89998 11.995C1.60126 12.3051 1.42814 12.7237 1.41707 13.1617L1.33345 16.6324L4.64282 16.6329C5.03222 16.6329 5.4067 16.4906 5.70228 16.2336L5.8093 16.1319L13.855 7.78129L9.906 3.68365ZM14.2437 1.93715C13.5113 1.17695 12.323 1.17695 11.5905 1.93715L10.838 2.71694L14.786 6.81459L15.5381 6.03433C16.2298 5.31636 16.2683 4.17634 15.6534 3.41148L15.5381 3.28056L14.2437 1.93715Z"
                    fill="white"
                  />
                </svg>
              </button>
            </div>
          </form>
        </div>

        <ToDoList setTodos={setTodos} todos={todos} />
      </div>
    </div>
  );
};

export default Form;
