import "./index.css";

const Clock = () => {
  let newDate = new Date();
  let hour = newDate.getHours();
  let minute = newDate.getMinutes().toLocaleString();

  function getTime(h) {
    if (h < 12) {
      return "morning";
    } else if (h < 18) {
      return "afternoon";
    } else if (h > 18) {
      return "night";
    } else {
      return "";
    }
  }
  return (
    <div className="clock">
      <div className="flex flex-col items-center justify-center mx-4 mt-10 mb-4 md:mx-0">
        <h1 className="my-4 text-3xl font-bold text-center text-white">
          Simple To-do List APP With React
        </h1>
        <div className="flex flex-col items-center justify-center w-full mx-4 md:w-2/6 card md:px-0 ">
          <div className="py-12 text-center">
            <h1 className="font-bold text-white text-7xl">{`${hour} : ${minute}`}</h1>
            <h2 className="text-4xl font-bold text-white">
              {`Good ${getTime(hour)}`}
            </h2>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Clock;
