import "./index.css";
import Todo from "./Todo";

const ToDoList = ({ setTodos, todos }) => {
  return (
    <div className="flex flex-wrap items-center justify-center">
      {todos.map((todo) => {
        return (
          <Todo
            todo={todo}
            text={todo.text}
            key={todo.id}
            todos={todos}
            setTodos={setTodos}
          />
        );
      })}
    </div>
  );
};

export default ToDoList;
