import { useState } from "react";
import "./App.css";
import Clock from "./Clock";
import Form from "./Form";
import "./index.css";

function App() {
  const [inputText, setInputText] = useState("");
  const [todos, setTodos] = useState([]);
  return (
    <div>
      <div className="container-md">
        <Clock />
        <Form
          setInputText={setInputText}
          todos={todos}
          setTodos={setTodos}
          inputText={inputText}
        />
      </div>
    </div>
  );
}

export default App;
