const Todo = ({ text, key, todos, setTodos, todo }) => {
  const deleteHandler = (e) => {
    e.preventDefault();
    setTodos(todos.filter((el) => el.id !== todo.id));
  };

  const completeHandler = (e) => {
    e.preventDefault();
    setTodos(
      todos.map((item) => {
        if (item.id === todo.id) {
          return {
            ...item,
            completed: !item.completed,
          };
        }
        return item;
      })
    );
  };
  return (
    <form className="flex items-center justify-center w-11/12 my-2 ">
      <div className="flex items-center justify-between bg-white rounded-full">
        <input
          type="text"
          value={text}
          disabled
          className={`w-10/12 h-10 pl-4 text-sm bg-transparent focus:outline-none ${
            todo.completed ? "line-through text-gray-500" : ""
          }`}
        />
        <button
          className="p-2 mr-1 rounded-full bg-lightPurple hover:bg-pink-500 focus:outline-none"
          onClick={completeHandler}>
          <svg
            width="18"
            height="19"
            viewBox="0 0 18 19"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path
              d="M12.9006 0.00119019C15.964 0.00119019 18 2.18774 18 5.32559V12.6768C18 15.8148 15.9639 18.0012 12.8997 18.0012H5.0985C2.03523 18.0012 0 15.8148 0 12.6768V5.32559C0 2.19053 2.04068 0.00119019 5.0985 0.00119019H12.9006ZM12.9006 1.35119H5.0985C2.80831 1.35119 1.35 2.91573 1.35 5.32559V12.6768C1.35 15.09 2.80328 16.6512 5.0985 16.6512H12.8997C15.1959 16.6512 16.65 15.0898 16.65 12.6768V5.32559C16.65 2.91266 15.1961 1.35119 12.9006 1.35119ZM12.6808 6.38819C12.9205 6.62783 12.9423 7.00283 12.7462 7.26708L12.6808 7.34279L8.40945 11.6142C8.16984 11.8538 7.79491 11.8756 7.53066 11.6796L7.45495 11.6143L5.31835 9.47859C5.05469 9.21504 5.0546 8.78765 5.31815 8.52399C5.55774 8.2843 5.93273 8.26244 6.19703 8.45845L6.27275 8.52379L7.93134 10.1818L11.7263 6.38819C11.9899 6.12459 12.4172 6.12459 12.6808 6.38819Z"
              fill="white"
            />
          </svg>
        </button>
        <button
          className="p-2 mr-1 rounded-full bg-darkPurple hover:bg-purple-800 focus:outline-none"
          onClick={deleteHandler}>
          <svg
            width="18"
            height="19"
            viewBox="0 0 18 19"
            fill="none"
            xmlns="http://www.w3.org/2000/svg">
            <path
              d="M15.8789 6.04813L15.9784 6.04935C16.3475 6.07681 16.6304 6.35263 16.65 6.68464L16.6405 6.86976L16.326 10.3359L15.9961 13.7184C15.9263 14.3937 15.8638 14.9632 15.8099 15.4116C15.6225 16.9741 14.4955 17.9403 12.7966 17.9689C10.1494 18.0129 7.60477 18.0125 5.13373 17.9644C3.48398 17.9332 2.37366 16.9565 2.18955 15.4179L2.0623 14.2843L1.83994 12.0855L1.61216 9.67265L1.35172 6.77628C1.31935 6.40467 1.61951 6.07921 2.02215 6.04933C2.39123 6.02195 2.7183 6.25245 2.79519 6.57729L2.82511 6.84275L3.06966 9.55803L3.33669 12.3725C3.45646 13.5908 3.56034 14.5769 3.64346 15.2695C3.74838 16.1463 4.26138 16.5975 5.16411 16.6146C7.61585 16.6623 10.1415 16.6628 12.7701 16.6191C13.7277 16.6029 14.2488 16.1561 14.356 15.2628L14.4827 14.1353C14.5198 13.7878 14.5594 13.4042 14.6013 12.9875L14.8686 10.2196L15.1906 6.66794C15.2204 6.32731 15.5192 6.0662 15.8789 6.04813ZM0.73139 4.49145C0.327455 4.49145 0 4.18924 0 3.81643C0 3.47469 0.275153 3.19227 0.632145 3.14757L0.73139 3.14141H3.91772C4.29636 3.14141 4.62785 2.91654 4.74642 2.59255L4.77543 2.49251L5.02304 1.35598C5.24111 0.603259 5.9492 0.067334 6.779 0.00688853L6.93592 0.00119019H11.0639C11.9075 0.00119019 12.6523 0.492838 12.9391 1.25254L12.9874 1.39807L13.2243 2.49224C13.2987 2.83504 13.6025 3.09107 13.9701 3.13478L14.0821 3.14141H17.2686C17.6725 3.14141 18 3.44362 18 3.81643C18 4.15817 17.7248 4.44059 17.3679 4.48529L17.2686 4.49145H0.73139ZM11.0639 1.35124H6.93592C6.7307 1.35124 6.54829 1.46209 6.47252 1.60141L6.44682 1.66262L6.20979 2.75734C6.1808 2.89095 6.13879 3.01961 6.08512 3.14229L11.9148 3.14247C11.8813 3.06593 11.8523 2.98705 11.8282 2.9061L11.79 2.75707L11.5636 1.70471C11.5107 1.52211 11.3473 1.38843 11.1502 1.35787L11.0639 1.35124Z"
              fill="white"
            />
          </svg>
        </button>
      </div>
    </form>
  );
};

export default Todo;
